import { RequestCache } from '@msdyn365-commerce/cache-internal';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { ActionContext } from '../../action-context';
import ProductList from './product-list';
import ProductSearchList from './product-search';

const defaultActionContext = new ActionContext({
    id: 'client',
    requestContext: {},
    requestCache: new RequestCache(),
    appCache: null
});

// window['cache'] = cache;
class App extends React.Component<any, any> {
    public render() {
        return (
            <>
                <h2>Action Layer Demo</h2>

                <ProductList channel='top-free' actionContext={defaultActionContext} />

                <ProductList channel='trending' actionContext={defaultActionContext} />

                <ProductList channel='top-paid' actionContext={defaultActionContext} />

                <ProductSearchList actionContext={defaultActionContext} />
            </>
        );
    }
}

ReactDOM.render(<App />, document.getElementById('root'));
